<template>
  <span
    class="glyphicon glyphicon-arrow-up"
    v-if="btnFlag"
    @click="backTop"
  ></span>
</template>

<script>
export default {
  data() {
    return {
      btnFlag: false,
    };
  },
  methods: {
    // 点击图片回到顶部方法，加计时器是为了过渡顺滑
    backTop() {
      const that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },

    // 为了计算距离顶部的高度，当高度大于60显示回顶部图标，小于60则隐藏
    scrollToTop() {
      const that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 60) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },
};
</script>

<style scoped>
* {
  padding: 0;
  border: 0;
  margin: 0;
}

span {
  position: fixed;
  left: 85%;
  top: 75%;
  width: 60px;
  height: 60px;
  line-height: 60px;
  border-radius: 30px;
  font-size: 30px;
  text-align: center;
  color: black;
}

span:hover {
  background-color: rgb(240, 240, 240);
}
</style>